/**=====================
     breadscrumb css start
==========================**/
.breadscrumb-section {
    background-color: #f2f2ee;
    position: relative;
    overflow: hidden;

    .circle {
        position: absolute;
        @include center(vertical);
        left: -210px;
        width: 475px;
        height: 475px;
        border-radius: 50%;
        background-color: #eaeaea;
    }

    .breadscrumb-contain {
        padding: calc(28px + (50 - 28) * ((100vw - 320px) / (1920 - 320))) 0;
        text-align: center;
        color: $title-color;
        font-family: $public-sans;

        @include mq-max(xs) {
            display: block;
        }

        h2 {
            font-weight: 700;
            font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
            margin-bottom: 25px;

            @include mq-max(xs) {
                text-align: center;
                margin-bottom: 8px;
            }
        }

        .search-box-breadscrumb {
            position: relative;
            width: 70%;
            margin: 0 auto;

            @include mq-max(sm) {
                width: 90%;
            }

            @include mq-max(2xs) {
                width: 100%;
            }

            input {
                width: 100%;
                border: none;
                border-radius: 6px;
                // padding: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320))) calc(14px + (24 - 14) * ((100vw - 320px) / (1920 - 320)));
                font-size: 15px;
            }

            i {
                position: absolute;
                @include center(vertical);
                right: calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
                ;
                color: $content-color;
                font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
                ;
            }
        }

        nav {
            .breadcrumb {
                @include flex_common;

                .breadcrumb-item {
                    font-weight: 700;

                    i {
                        color: $content-color;
                    }

                    &.active {
                        color: $black;
                    }

                    +.breadcrumb-item {
                        position: relative;

                        &::before {
                            content: "\f105";
                            @include font;
                            color: $black;
                        }
                    }
                }
            }
        }
    }
}